import React from 'react';
let Ensure=React.createClass({
    cancel(){
        "use strict";
        let mask=document.getElementById("dialogContainer");
        mask.style.display="none";
    },
    ensure(){
        "use strict";
        let mask=document.getElementById("dialogContainer");
        mask.style.display="none";
    },

    render(){
        "use strict";
        return(
            <div className="dialog">
                <h2 className="title"><i onClick={this.cancel}/></h2>
                <div className="dialog-assign-driver" >
                    <ul>
                        <li><p></p><p className="urgent">15</p><p></p></li>
                        <li><p></p><p className="sending"></p><p></p></li>
                        <li><p></p><p className="taking"></p><p></p></li>
                        <li><p></p><p></p><p></p></li>
                    </ul>
                </div>
                <section className="btn">
                    <button onClick={this.cancel}></button>
                    <button onClick={this.ensure}></button>
                </section>
            </div>
        );
    }
});

export default Ensure;



