import React from 'react';
let SendCar=React.createClass({
    getInitialState(){
        "use strict";
        return{
            status:0
        };
    },
    render(){
        "use strict";
        let html=null;
        if(this.state.status==1){
            html=(<div className="take-car"><p className="pre-send-car"></p></div>);
        }else{
            html=(<div className="take-car">
                    <section className="up-part">
                        <p><label>:&ensp;</label><span>&ensp;</span><em></em></p>
                        <p><label>:&ensp;</label><span>2016-11-25 18:54</span></p>
                    </section>
                    <section className="down-part">
                        <p><label>:&ensp;</label><span>2016-11-25 18:54&ensp;</span><em></em></p>
                        <p><label>:&ensp;</label><span>2016-11-25 18:54</span></p>
                    </section>
                    <div className="service-fee">
                        <p><label>:&ensp;</label><span>189(59+30+100)</span></p>
                        <p><label>:&ensp;</label><span>100</span></p>
                        <p><label>:&ensp;</label><span>100</span></p>
                    </div>
                </div>);
        }

        return html;
    }
});

export default SendCar;



