import React from 'react';

import TextScroll from '../widgets/text_scroll';
import TextInput from '../widgets/text_input';
import TableHead from '../widgets/table_head';
import CouponLine from '../widgets/coupon_line';

export default React.createClass({
    getInitialState(){
        "use strict";
        return{
            queryCondition:{
                phone_no:"",
                wx_nick:""
            }
        };
    },
    handleChange(e){
        "use strict";
        let key=e.target.id;
        let val=e.target.value;
        if(key==="phone_no"){
            this.state.queryCondition.phone_no=val;
        }else if(key==="wx_nick"){
            this.state.queryCondition.wx_nick=val;
        }
    },
    handleQuery(){
        "use strict";
        console.log(this.state.queryCondition);
    },
    render(){
        "use strict";
        let widths=['170px','180px','190px','190px','190px','200px','180px','180px','150px'];
        let headData= [
            {name:'ID',width:'170px'},
            {name:'',width:'180px'},
            {name:'',width:'190px'},
            {name:'',width:'190px'},
            {name:'',width:'190px'},
            {name:'',width:'200px'},
            {name:'',width:'180px'},
            {name:'',width:'180px'},
            {name:'',width:'150px'}];

        let data=[{id:'1457',fieldName:'CouponID'},
            {phone_no:"1457258456",fieldName:'PhoneNo'},
            {type_msg:"()",fieldName:'CouponType'},
            {status:"",fieldName:'CouponStatus'},
            {amount:'2',fieldName:'MoneyAmount'},
            {receive_time:'2016-8-9',fieldName:'ReceiveCouponTime'},
            {deadline_time:'2016-8-9',fieldName:'DeadlineTime'},
            {source:'',fieldName:'ActivitySource'},
            {op_items:[""],dialogs:[1],color:"#1A9FE5",fieldName:'Operation'}];
        return(
            <section className="data-section">
                <TextScroll />
                <div className="query-condition">
                    <TextInput title=":" change={this.handleChange} name="phone_no" holdText=""/>
                    <TextInput title=":" change={this.handleChange} name="wx_nick" holdText="" />
                    <button className="query-btn" onClick={this.handleQuery}></button>
                    <button className="checkout" ></button>
                </div>
                <TableHead data={headData} />
                <CouponLine widths={widths} data={data} />
                <CouponLine widths={widths} data={data} />
                <CouponLine widths={widths} data={data} />
                <CouponLine widths={widths} data={data} />
            </section>
        );
    }
});



