import React from 'react';
import ReactDOM from 'react-dom';
import TakeCar from '../order_process/take_car';
import MoveCar from '../order_process/move_car';
import InGarage from '../order_process/in_garage';
import SendCar from '../order_process/send_car';
import Evaluation from '../order_process/evaluation';

let OrderDetail=React.createClass({
    getInitialState(){
        "use strict";
        return{
            p_item:'p1'
        };
    },
    componentDidMount(){
        ReactDOM.render(<TakeCar /> , this.refs.processInfo);
    },
    handleSwitch(e){
        "use strict";
        if(e.target.nodeName==="LI"){
            if (e.target.id == "pro_1") {
                this.setState({p_item:'p1'});
                ReactDOM.render(<TakeCar /> , this.refs.processInfo);
            } else if (e.target.id == "pro_2") {
                this.setState({p_item:'p2'});
                ReactDOM.render(<MoveCar /> , this.refs.processInfo);
            } else if (e.target.id == "pro_3") {
                this.setState({p_item:'p3'});
                ReactDOM.render(<InGarage /> , this.refs.processInfo);
            } else if (e.target.id == "pro_4") {
                this.setState({p_item:'p4'});
                ReactDOM.render(<SendCar /> , this.refs.processInfo);
            } else if (e.target.id == "pro_5") {
                this.setState({p_item:'p5'});
            } else if (e.target.id == "pro_6") {
                this.setState({p_item:'p6'});
                ReactDOM.render(<Evaluation /> , this.refs.processInfo);
            }
        }
    },
    render(){
        "use strict";
        return(
            <section className="detail-section">
                <p className="order-brief">
                    <label style={{paddingLeft:'20px'}}>: </label><span>8745456765454</span>
                    <label style={{paddingLeft:'20px'}}>: </label><span>2016-12-12 14:12</span>
                    <label style={{paddingLeft:'20px'}}>: </label><span></span>
                    <label style={{paddingLeft:'20px'}}>: </label><span style={{color:'red'}}></span>
                </p>
                <div className="order-main">
                    <div className="user-info">
                        <h2></h2>
                        <figure className="user-basic">
                            <img src="/img/admin/userheadimg.png"/>
                            <figcaption>
                                <p>: <span style={{color:"#1AA0E5"}}></span></p>
                                <p>: <span></span></p>
                                <p>: <span>145785245</span></p>
                            </figcaption>
                        </figure>
                        <div className="user-other">
                            <p><label>:</label><span style={{color:'red'}}>&#9733;</span></p>
                            <p><label>:</label><span>5</span></p>
                            <p><label>:</label><span></span></p>
                            <p><label>:</label><span>2016-11-18 18:45</span></p>
                            <p><label>&emsp;&emsp;:</label><span style={{color:"#1AA0E5"}}></span></p>
                            <p className="note-field"><label>&emsp;&emsp;: </label>
                                <span></span></p>
                        </div>
                    </div>
                    <div className="order-info">
                        <h2></h2>
                        <div className="up-section">
                            <p><label>:&ensp;</label>
                                <span style={{color:"#1AA0E5"}}>457887 </span></p>
                            <p><label>:&ensp;</label>
                                <span style={{color:"#1AA0E5"}}>2016-12-12 18:45</span></p>
                            <p><label>:&ensp;</label>
                                <span>T2</span></p>
                        </div>
                        <div className="down-section">
                            <p><label>:&ensp;</label><span>2016-12-12 18:45</span></p>
                            <p className="back-flight">
                                <label>:&ensp;</label>
                                <span style={{color:"#1AA0E5"}}>hu98547_2016-11-12</span>
                                <img src="/img/admin/icon/10_1.png" />
                            </p>
                            <p><label>:&ensp;</label>
                                <span style={{color:"#1AA0E5"}}>2016-12-14 17:25</span></p>
                            <p><label>:&ensp;</label><span>T1</span></p>
                            <p className="note-field"><label>:</label>
                                <span>askaskask</span></p>
                        </div>
                    </div>
                    <div className="service-info">
                        <h2></h2>
                        <div className="extra-service">
                            <p><label>:</label><span></span>
                                <em></em><em></em>
                            </p>
                            <p style={{marginBottom:'62px'}}><label>:</label><span></span>
                                <em></em>
                            </p>
                        </div>
                        <p className="note-field"><label>:</label><span></span></p>
                    </div>
                    <div className="process-info" >
                        <ul onClick={this.handleSwitch}>
                            <li className={this.state.p_item=='p1'?"show-item":''} id="pro_1" ></li>
                            <li id="pro_2" className={this.state.p_item=='p2'?"show-item":''} ></li>
                            <li id="pro_3" className={this.state.p_item=='p3'?"show-item":''} ></li>
                            <li id="pro_4" className={this.state.p_item=='p4'?"show-item":''} ></li>
                            <li id="pro_5" className={this.state.p_item=='p5'?"show-item":''} ></li>
                            <li id="pro_6" className={this.state.p_item=='p6'?"show-item":''} ></li>
                        </ul>
                        <div ref="processInfo">

                        </div>
                    </div>
                    <div className="service-note">
                        <p>:<img src="/img/admin/icon/13_1.png"/></p>
                        <p>2016-11-18 18:34&ensp;:&ensp;</p>
                        <p>2016-11-18 18:34&ensp;:&ensp;</p>
                    </div>
                    <div className="driver-note">
                        <p>:</p>
                        <p>2016-11-18 18:34&ensp;<span style={{color:'yellow'}}>:&ensp;</span></p>
                        <p>2016-11-18 18:34&ensp;<span style={{color:'#1AA0E5'}}>:&ensp;</span></p>
                    </div>
                </div>
            </section>
        );
    }
});

export default OrderDetail;


