import React from 'react';
let TakeCar=React.createClass({
    getInitialState(){
        "use strict";
        return{
            status:1
        };
    },
    render(){
        "use strict";
        let html=null;
        if(this.state.status==0){
            html=(<div className="take-car"><p className="pre-take-car"></p></div>);
        }else{
            html=(<div className="take-car">
                    <section className="up-part">
                        <p><label>:&ensp;</label><span>&ensp;</span><em></em></p>
                        <p><label>:&ensp;</label><span>2016-11-25 18:54</span></p>
                    </section>
                    <section className="down-part">
                        <p><label>:&ensp;</label><span>2016-11-25 18:54&ensp;</span><em></em></p>
                        <p><label>:&ensp;</label><span>2016-11-25 18:54</span></p>
                    </section>

                </div>);
        }

        return html;
    }
});

export default TakeCar;



