import React from 'react';

import TextScroll from '../widgets/text_scroll';
import TextInput from '../widgets/text_input';
import SelectInput from '../widgets/select_input';
import TableHead from '../widgets/table_head';
import TableLine from '../widgets/table_line';

let EvaluationManage=React.createClass({
    getInitialState(){
        "use strict";
        return{
            queryCondition:{
                order_source:"",
                order_no:"",
                phone_no:""
            }
        };
    },
    handleChange(e){
        "use strict";
        let key=e.target.id;
        let val=e.target.value;
        if(key==="phone_no"){
            this.state.queryCondition.phone_no=val;
        }else if(key==="order_source"){
            this.state.queryCondition.order_source=val;
        }else if(key==="order_no"){
            this.state.queryCondition.order_no=val;
        }
    },
    handleQuery(){
        "use strict";
        console.log(this.state.queryCondition);
    },
    render(){
        "use strict";
        let widths=['180px','180px','180px','180px','200px','190px','200px','160px','150px'];
        let headData=[
            {name:'',width:'180px'},
            {name:'',width:'180px'},
            {name:'',width:'180px'},
            {name:'',width:'180px'},
            {name:'',width:'200px'},
            {name:'',width:'190px'},
            {name:'',width:'200px'},
            {name:'',width:'160px'},
            {name:'',width:'150px'}];

        let data=[{order_no:'1445515665454',fieldName:'OrderNo'},
            {username:"",phone_no:"124578654",fieldName:'User'},
            {car_no:'B4878',car_color:'',car_brand:'',fieldName:'Car'},
            {airport:'',fieldName:'Airport'},
            {evaluate_time:'2016-12-12 14:14',fieldName:'EvaluateTime'},
            {evaluate_star_level:5,fieldName:'EvaluateStarLevel'},
            {service_reply:"",fieldName:'CustomerServiceReply'},
            {status:"",fieldName:'ShowStatus'},
            {op_items:['',''],dialogs:[1,2],color:"#1A9FE5",fieldName:'Operation'}];
        return(
            <section className="data-section">
                <TextScroll />
                <div className="query-condition">

                    <TextInput title=":" change={this.handleChange} name="phone_no" holdText=""/>
                    <SelectInput title=":" change={this.handleChange} name="order_source" defaultName=""/>
                    <SelectInput title=":" change={this.handleChange} name="order_source" defaultName=""/>
                    <SelectInput title=":" change={this.handleChange} name="order_source" defaultName=""/>
                    <hr/>
                    <SelectInput title=":" change={this.handleChange} name="order_source" defaultName=""/>
                    <SelectInput title=":" change={this.handleChange} name="order_source" defaultName=""/>
                    <SelectInput title=":" change={this.handleChange} name="order_source" defaultName=""/>
                    <button className="query-btn" onClick={this.handleQuery}></button>
                </div>
                <TableHead data={headData} />
                <TableLine widths={widths} data={data} />
                <TableLine widths={widths} data={data} />
                <TableLine widths={widths} data={data} />
                <TableLine widths={widths} data={data} />
            </section>
        );
    }
});

export default EvaluationManage;


