import React from 'react';

let SelectInput=React.createClass({
    render(){
        "use strict";
        let dataList=[{name:"",value:"SZAIR"},{name:"",value:"Ctrip"},
            {name:"",value:"CTRIPSHEETSELL"}, {name:"",value:"WUHANSHENGYI"},
            {name:"",value:"TONGCHENG"},{name:"",value:"DISTRIBUTOR"},{name:"",value:"CCB"}];
        let list=dataList.map(function (item,index) {
            return(
                <option value={item.value} key={index}>{item.name}</option>
            );
        });
        return(
            <p className="input-item select-item">
                <label>{this.props.title}</label>
                <select id={this.props.name}  onChange={this.props.change} >
                    <option value="">{this.props.defaultName}</option>
                    {list}
                </select>
            </p>
        );
    }
});

export default SelectInput;


