import React from 'react';

import TextScroll from '../widgets/text_scroll';
import TextInput from '../widgets/text_input';
import TableHead from '../widgets/table_head';
import UserLine from '../widgets/user_line';

let UserManager=React.createClass({
    getInitialState(){
        "use strict";
        return{
            queryCondition:{
                phone_no:""
            }
        };
    },
    handleChange(e){
        "use strict";
        let key=e.target.id;
        let val=e.target.value;
        if(key==="phone_no"){
            this.state.queryCondition.phone_no=val;
        }
    },
    handleQuery(){
        "use strict";
        console.log(this.state.queryCondition);
    },
    render(){
        "use strict";
        let widths=['230px','216px','210px','210px','210px','240px','180px','150px'];
        let headData=[{name:'',width:'230px'},
            {name:'',width:'210px'}, {name:'',width:'216px'},
            {name:'',width:'210px'},{name:'',width:'210px'},
            {name:'',width:'240px'},{name:'',width:'180px'},
            {name:'',width:'150px'}];

        let data=[{phone_no:'14572584545',fieldName:'PhoneNo'},
            {full_name:"",fieldName:'FullName'},
            {gender:"",fieldName:'Gender'},
            {level:3,fieldName:'ImportantLevel'},
            {mark:'',fieldName:'Mark'},
            {mark_star_time:'2016-8-9 15:14',fieldName:'MarkStarTime'},
            {logon_time:'2016-8-9 15:14',fieldName:'LogonTime'},
            {op_items:["",""],dialogs:[2],color:"#1A9FE5",fieldName:'Operation'}];
        return(
            <section className="data-section">
                <TextScroll />
                <div className="query-condition">
                    <TextInput title=":" change={this.handleChange} name="phone_no" holdText=""/>
                    <button className="query-btn" onClick={this.handleQuery}></button>
                    <button className="checkout" ></button>
                </div>
                <TableHead data={headData} />
                <UserLine widths={widths} data={data} />
                <UserLine widths={widths} data={data} />
                <UserLine widths={widths} data={data} />
                <UserLine widths={widths} data={data} />
            </section>
        );
    }
});

export default UserManager;


