import React from 'react';
import ImgScroll from '../widgets/img_scroll';
let TakeCar=React.createClass({
    getInitialState(){
        "use strict";
        return{
            status:0
        };
    },
    render(){
        "use strict";
        let imgData=["/img/admin/carimg.png","/img/admin/carimg.png","/img/admin/carimg.png"];
        return (<div className="move-car">
                   <ImgScroll imgs={imgData} />
                    <section className="move-car-msg">
                        <p><label>:&ensp;</label><span></span></p>
                        <p><label>:&ensp;</label><span>2016-11-25 18:54</span></p>
                        <p><label>:&ensp;</label><span>2016-11-25 18:54</span></p>
                        <p><label>:&ensp;</label><span></span></p>
                    </section>
                </div>);
    }
});

export default TakeCar;



